"use client";

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Mail, Lock, Eye, EyeOff, ArrowRight, Loader2 } from 'lucide-react';

const LoginPage = () => {
    const router = useRouter();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [rememberMe, setRememberMe] = useState(false);
    const [showPassword, setShowPassword] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);

        // Mock authentication delay
        setTimeout(() => {
            router.push('/dashboard');
        }, 1500);
    };

    return (
        <div className="min-h-screen w-full flex items-center justify-center bg-[#f8fafc] relative overflow-hidden font-secondary">
            {/* Dynamic Background Elements */}
            <div className="absolute inset-0 z-0 overflow-hidden pointer-events-none">
                <div className="absolute -top-[10%] -left-[5%] w-[50%] h-[70%] rounded-full bg-primary/15 blur-[120px] animate-blob" />
                <div className="absolute top-[20%] -right-[10%] w-[45%] h-[60%] rounded-full bg-blue-400/10 blur-[100px] animate-blob animation-delay-2000" />
                <div className="absolute -bottom-[20%] left-[20%] w-[40%] h-[50%] rounded-full bg-indigo-400/10 blur-[100px] animate-blob animation-delay-4000" />
            </div>

            <div className="w-full max-w-[1050px] p-6 z-10 flex justify-center items-center">
                <div className="w-full bg-white/40 backdrop-blur-3xl border border-white/50 rounded-3xl flex flex-col md:flex-row overflow-hidden shadow-[0_25px_50px_-12px_rgba(0,0,0,0.08)] ring-1 ring-black/5">

                    {/* Left side - Welcome info/Branding */}
                    <div className="hidden md:flex flex-1 bg-gradient-to-br from-primary via-primary to-indigo-900 p-12 text-white flex-col justify-between relative overflow-hidden">
                        <div className="relative z-10">
                            <div className="flex items-center gap-5 mb-14 group cursor-default">
                                <img
                                    src="/assets/logo.png"
                                    alt="RCA Logo"
                                    className="w-16 h-16 object-contain grayscale brightness-200 group-hover:grayscale-0 group-hover:brightness-100 transition-all duration-700 hover:scale-110"
                                />
                                <div className="flex flex-col -space-y-1">
                                    <span className="text-32px font-bold text-white tracking-tighter leading-none">Rwanda Coding</span>
                                    <span className="text-18px font-bold text-white/70 tracking-[0.2em] uppercase">Academy</span>
                                </div>
                            </div>
                            <p className="text-18px text-white/80 leading-relaxed max-w-[320px] font-medium border-l-2 border-white/20 pl-6">
                                A specialized platform for healthcare professionals to manage student wellness and medical records with precision.
                            </p>
                        </div>

                        <div className="relative z-10 mt-auto">
                            <div className="flex items-center gap-4 text-white/60 mb-8 font-medium italic">
                                <span className="h-px w-8 bg-white/20"></span>
                                <span>Premium Healthcare Standard</span>
                            </div>
                        </div>

                        {/* Abstract decorative shapes */}
                        <div className="absolute top-[-10%] right-[-10%] w-64 h-64 bg-white/5 rounded-full blur-3xl animate-pulse" />
                        <div className="absolute bottom-[20%] left-[-5%] w-32 h-32 bg-indigo-400/20 rounded-full blur-2xl animate-bounce-slow" />
                        <div className="absolute inset-0 bg-[url('https://www.transparenttextures.com/patterns/cubes.png')] opacity-5 mix-blend-overlay"></div>
                    </div>

                    {/* Right side - Login Form */}
                    <div className="flex-1 p-8 md:p-14 flex flex-col justify-center bg-white/60 backdrop-blur-sm">
                        <div className="w-full max-w-[380px] mx-auto">
                            <div className="mb-10">
                                <h1 className="text-32px font-bold text-slate-900 mb-3 tracking-tight">Portal Access</h1>
                                <p className="text-slate-500 text-15px">Sign in to manage patient records and consultations.</p>
                            </div>

                            <form onSubmit={handleSubmit} className="space-y-6">
                                <div className="space-y-2 group">
                                    <label htmlFor="email" className="text-13px font-bold text-slate-700 uppercase tracking-wider ml-1">Email Address</label>
                                    <div className="relative flex items-center transition-all duration-300">
                                        <Mail className="absolute left-4 text-slate-400 group-focus-within:text-primary transition-colors" size={18} />
                                        <input
                                            type="email"
                                            id="email"
                                            placeholder="nurse@rca.ac.rw"
                                            value={email}
                                            onChange={(e) => setEmail(e.target.value)}
                                            required
                                            className="w-full py-3.5 pl-12 pr-4 bg-slate-50 border border-slate-200 rounded-16 text-15px outline-none focus:bg-white focus:border-primary focus:ring-4 focus:ring-primary/5 transition-all shadow-sm"
                                        />
                                    </div>
                                </div>

                                <div className="space-y-2 group">
                                    <label htmlFor="password" className="text-13px font-bold text-slate-700 uppercase tracking-wider ml-1">Password</label>
                                    <div className="relative flex items-center transition-all duration-300">
                                        <Lock className="absolute left-4 text-slate-400 group-focus-within:text-primary transition-colors" size={18} />
                                        <input
                                            type={showPassword ? 'text' : 'password'}
                                            id="password"
                                            placeholder="••••••••"
                                            value={password}
                                            onChange={(e) => setPassword(e.target.value)}
                                            required
                                            className="w-full py-3.5 pl-12 pr-12 bg-slate-50 border border-slate-200 rounded-16 text-15px outline-none focus:bg-white focus:border-primary focus:ring-4 focus:ring-primary/5 transition-all shadow-sm"
                                        />
                                        <button
                                            type="button"
                                            className="absolute right-3 p-1.5 text-slate-400 hover:text-slate-600 transition-colors bg-white/50 rounded-lg"
                                            onClick={() => setShowPassword(!showPassword)}
                                        >
                                            {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                                        </button>
                                    </div>
                                </div>

                                <div className="flex justify-between items-center text-13px">
                                    <label className="flex items-center gap-2 cursor-pointer group">
                                        <div className="relative w-5 h-5 flex items-center justify-center">
                                            <input
                                                type="checkbox"
                                                checked={rememberMe}
                                                onChange={(e) => setRememberMe(e.target.checked)}
                                                className="peer absolute opacity-0 cursor-pointer"
                                            />
                                            <div className="w-full h-full bg-slate-100 border border-slate-300 rounded-6 peer-checked:bg-primary peer-checked:border-primary transition-all ring-offset-2 peer-focus:ring-2 ring-primary/20" />
                                            <svg className="absolute w-3.5 h-3.5 text-white opacity-0 peer-checked:opacity-100 transition-opacity" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round">
                                                <polyline points="20 6 9 17 4 12" />
                                            </svg>
                                        </div>
                                        <span className="text-slate-600 font-medium select-none">Stay logged in</span>
                                    </label>
                                    <a href="#" className="text-primary font-bold hover:text-primary-dark transition-colors">Recovery options</a>
                                </div>

                                <button
                                    type="submit"
                                    disabled={isLoading}
                                    className="w-full py-4.5 bg-primary text-white rounded-16 text-16px font-bold flex items-center justify-center gap-3 hover:bg-primary-dark shadow-[0_10px_20px_-5px_rgba(26,38,74,0.3)] hover:shadow-[0_15px_30px_-5px_rgba(26,38,74,0.4)] hover:-translate-y-0.5 active:translate-y-0 transition-all disabled:opacity-70 disabled:pointer-events-none mt-4 group"
                                >
                                    {isLoading ? (
                                        <>
                                            <Loader2 className="animate-spin" size={20} />
                                            <span>Authenticating...</span>
                                        </>
                                    ) : (
                                        <>
                                            <span>Secure Sign In</span>
                                            <ArrowRight size={18} className="group-hover:translate-x-1 transition-transform" />
                                        </>
                                    )}
                                </button>
                            </form>

                            <div className="mt-12 text-center">
                                <p className="text-slate-500 text-14px">
                                    Need access? <a href="#" className="text-primary font-bold decoration-2 hover:underline underline-offset-4">Contact Administrator</a>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <style jsx global>{`
                @keyframes blob {
                    0% { transform: translate(0px, 0px) scale(1); }
                    33% { transform: translate(30px, -50px) scale(1.1); }
                    66% { transform: translate(-20px, 20px) scale(0.9); }
                    100% { transform: translate(0px, 0px) scale(1); }
                }
                .animate-blob {
                    animation: blob 7s infinite;
                }
                .animation-delay-2000 {
                    animation-delay: 2s;
                }
                .animation-delay-4000 {
                    animation-delay: 4s;
                }
                @keyframes bounce-slow {
                    0%, 100% { transform: translateY(0); }
                    50% { transform: translateY(-30px); }
                }
                .animate-bounce-slow {
                    animation: bounce-slow 6s ease-in-out infinite;
                }
            `}</style>
        </div>
    );
};

export default LoginPage;
